.header {
  padding-block: 47px;
  position: absolute;
  left: 0;
  right: 0;
}

.header__content, .header__nav-list, .header__actions {
  align-items: center;
  display: flex;
}

.header__content {
  justify-content: space-between;
}

.header__actions, .header__nav-list {
  gap: 35px;
}

.header__nav-link {
  color: #fff;
  font-weight: 500;
}

.footer {
  background-color: #05040b;
}

.footer__content {
  justify-content: space-between;
  align-items: center;
  padding-block: 44px;
  display: flex;
}

.footer__nav-list {
  gap: 45px;
  display: flex;
}

.footer__nav-link {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  line-height: 150%;
}

.socials__list {
  align-items: center;
  gap: 28px;
  display: flex;
}

.social__link {
  width: 24px;
  height: 24px;
  display: block;
}

.facebook {
  background-image: url("facebook.77ed1dac.svg");
}

.twitter {
  background-image: url("x.57a0bc78.svg");
}

.you-tube {
  background-image: url("you-tube.8c165c70.svg");
}

.reddit {
  background-image: url("reddit.789e522c.svg");
}

.hero {
  background: linear-gradient(130deg, #191536 0%, #000 100%);
  height: 100vh;
  padding: 196px 0 84px;
}

.hero__content {
  display: flex;
  position: relative;
}

.hero__content .play-video__button {
  margin-left: 116px;
}

.hero__left {
  flex-direction: column;
  width: 552px;
  margin-bottom: 212px;
  display: flex;
}

.hero__title {
  margin-bottom: 19px;
  margin-left: 24px;
  font-size: 54px;
  font-weight: 900;
  line-height: 130%;
}

.hero__title span {
  color: #05c2df;
  font-weight: 400;
}

.hero__description {
  color: #929292;
  max-width: 400px;
  margin-bottom: 30px;
  margin-left: 116px;
  font-size: 20px;
  line-height: 150%;
}

.hero__price {
  margin-bottom: 36px;
  margin-left: 116px;
  font-size: 20px;
}

.hero__right {
  width: 1104px;
  height: 595px;
  position: absolute;
  left: 360px;
}

.features {
  background-color: #05040b;
}

.features__content {
  padding-block: 159px;
}

.features__title {
  text-align: center;
  margin-bottom: 16px;
  font-size: 36px;
  font-weight: 900;
}

.features__description {
  text-align: center;
  color: #05c2df;
  margin-bottom: 70px;
  font-size: 14px;
  font-weight: 400;
}

.features__title span {
  color: #05c2df;
  font-weight: 400;
}

.features__list {
  justify-content: space-between;
  display: flex;
}

.features__item {
  flex-direction: column;
  align-items: center;
  width: 348px;
  display: flex;
}

.feature__title {
  margin-bottom: 20px;
  font-weight: 900;
}

.features__item p {
  text-align: center;
  color: #929292;
  line-height: 150%;
}

.feature__icon {
  width: 98px;
  height: 98px;
  margin-bottom: 30px;
}

.education__icon {
  background-image: url("education-icon.785d6509.svg");
}

.real-estate__icon {
  background-image: url("real-estate-icon.36606c1e.svg");
}

.fitness__icon {
  background-image: url("fitness-icon.967e2362.svg");
}

.interactions__icon {
  background-image: url("social-interactions-icon.75ecf8d1.svg");
}

.about {
  background: linear-gradient(130deg, #191536 0%, #000 100%);
}

.about__content {
  padding: 157px 0 146px;
}

.about__top {
  margin-bottom: 175px;
}

.about__slider {
  position: relative;
}

.about__current-slide {
  color: #05c2df;
  font-size: 14px;
  font-weight: 400;
  position: absolute;
  left: -34px;
}

.slider__viewport {
  margin-bottom: 12px;
}

.slider__status-bar {
  border-bottom: 1px solid #212121;
  justify-content: space-between;
  width: 210px;
  padding-bottom: 10px;
  display: flex;
  position: relative;
}

.slider__status-bar:after {
  content: "";
  background-color: #05c2df;
  width: 20%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider__btn {
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

.about-product__title {
  margin-bottom: 60px;
  font-size: 36px;
  font-weight: 900;
}

.about-product__title span {
  color: #05c2df;
  font-weight: 400;
}

.about-product__description {
  color: #929292;
  margin-bottom: 42px;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}

.about-us__greeting {
  color: #05c2df;
  grid-row: 2 / 3;
  margin-bottom: 15px;
  font-size: 14px;
  font-weight: 400;
}

.about-us__title {
  grid-row: 3 / 4;
  margin-bottom: 32px;
  font-size: 36px;
